import React,{useState} from 'react'
import Binary from "./Binary"
import "./Converter.css"

function Converter({converter}) {
    let btn = []
    let setter = []
    const [value, setValue] = useState("")
    const [binaries, setBinaries] = useState(setter)

    for (var i = 0; i < 48; i++){
        btn.push(i)
        setter.push(0)
    }

    const inputHandlre = e => {
        setValue(e.target.value)
        let binarify = e.target.value === "" ? "0" : parseInt(e.target.value).toString(2)
        let binarifyR = binarify.split("").reverse()
        let newArr = [...binaries]
        if(parseInt(e.target.value) >= 0 && parseInt(e.target.value) <= 281474976645119){
        for (let i = 0; i < binarify.length; i++){
            newArr[i] = binarifyR[i]
            setBinaries(newArr)
        }
        for (let j = binarify.length; j < 48; j++){
            newArr[j] = 0
            setBinaries(newArr)
        }}
    }
//  Limit MAX number on input
    return (
        <div className="converter" style={{display: converter ? "" : "none"}}>
            <div className="button_container">
            {
                btn.map((item, i) => (
                    <Binary key={item} id={i}
                        binaries={binaries} setBinaries={setBinaries}
                        value={value}
                        setValue={setValue}
                    />
                ))
                
                }
            </div>
            <div className="marker">
                <h1>0</h1>
                <h1>12</h1>
                <h1>23</h1>
                <h1>24</h1>
                <h1>36</h1>
                <h1>47</h1>
            </div>
            <input type="number" id="converterInput"
                autoComplete="off"
                value={value}
                onChange={inputHandlre}
            />
        </div>
    )
}

export default Converter
