import React, { useState } from 'react';
import './Basic.css';

function Advanced({advanced}) {
    const [caches, setCaches] = useState([]);
    const [newlist, setNewlist] = useState("")
    const [sum, setSum] = useState("")
    const [op, setOp] = useState("")
    const [sup, setSup] = useState(false)
    
    const equal = (e) => {
        const advancedInput = document.getElementById("advancedInput");
        if (advancedInput.value.length > 0) {
            let sumx = sum.map(x => +x);
            var equalize = sum.toString().length >= 1 ? sumx[0] : parseInt(sum);
            for (var i = 0; i <= op.length; i++) {
                if(op[i]==="+") equalize += sumx[i+1];
                if(op[i]==="-") equalize -= sumx[i+1];
                if(op[i]==="\u00D7") equalize *= sumx[i+1];
                if(op[i]==="\u00F7") equalize /= sumx[i+1];
                if (op[i] === "%") equalize *= 0.01;
            }
            setCaches([...caches,
                {
                    input: newlist,
                    sumnumber: !isNaN(equalize) ? (parseFloat(equalize.toFixed(4))) : "Syntax Error",
                }
            ])
            setNewlist(!isNaN(equalize) ? equalize : "Syntax Error");
            setSum(equalize.toString().split(/[%+\u00F7\u00D7-]/g));
            setOp("");
        }
    }
    
    const enter = (e) => {
        const advancedInput = document.getElementById("advancedInput");
        console.log("kaeey code: " + e.keyCode)
        if (sup) {
            e.preventDefault()
            switch (e.keyCode) {
                case 48: e.target.value += "\u2070"; break;
                case 49: e.target.value += "\u00B9"; break;
                case 50: e.target.value += "\u00B2"; break;
                case 51: e.target.value += "\u00B3"; break;
                case 52: e.target.value += "\u2074"; break;
                case 53: e.target.value += "\u2075"; break;
                case 54: e.target.value += "\u2076"; break;
                case 55: e.target.value += "\u2077"; break;
                case 56: e.target.value += "\u2078"; break;
                case 57: e.target.value += "\u2079"; break;
                        
                default: break
            }
            setNewlist(advancedInput.value);

        
    
        }
        switch (e.keyCode) {
            case 191: e.target.value += "\u00F7"; break
            case 56: if(e.shiftKey) e.target.value += "\u00D7"; break
            case 13: equal(); break
            default: break
        }
    
    }
    const addtoInput = (e) => {
        const advancedInput = document.getElementById("advancedInput");
        advancedInput.value += e.target.value;
        setNewlist(advancedInput.value);
        setSum(advancedInput.value.split(/[%+\u00F7\u00D7-]/g));
        setOp(advancedInput.value.split(/[0-9]|[.]/g).join("").split(""));
    } 

    const clear = (e) => {
        e.preventDefault();
        setNewlist("")
        setSum("")
        setOp("")
    } 

    const OnlyNumericalValue = (e) => {
        e.target.value = e.target.value.replace(/[^0-9%^.+\u00F7\u00D7\u00B9-]/g, '')
    }
    const inputHandlre = (e) => {
        setNewlist(e.target.value);
        setSum(e.target.value.split(/[%+\u00F7\u00D7-]/g))
        setOp(e.target.value.split(/[0-9]|[.]/g).join("").split(""))
    }
    const superscript = e => {
        e.preventDefault()
        setSup(!sup)
    }
    return (
        <div className="basic__container" style={{display: advanced ? "" : "none"}}>
            <div className="conter">
            {
            caches.map((cache, i) => (
                <div key={i} className="conterInner">
                    <h1 >{cache.input}</h1> 
                    <h1 >=</h1> 
                    <h1 >{cache.sumnumber}</h1>
                </div>
            ))
            }
            </div>
            <input type="text" id="advancedInput"
            value={newlist}
                onChange={inputHandlre}
                onKeyDown={enter}
                onInput={OnlyNumericalValue}
                autoComplete="off"
            />

            <div className="buttons">
            <button value={sup ? "\u2070" : "0"} className="n0" onMouseUp={addtoInput}>0</button>
            <button value={sup ? "\u00B9" : "1"} className="n1" onMouseUp={addtoInput}>1</button>
            <button value={sup ? "\u00B2" : "2"} className="n2" onMouseUp={addtoInput}>2</button>
            <button value={sup ? "\u00B3" : "3"} className="n3" onMouseUp={addtoInput}>3</button>
            <button value={sup ? "\u2074" : "4"} className="n4" onMouseUp={addtoInput}>4</button>
            <button value={sup ? "\u2075" : "5"} className="n5" onMouseUp={addtoInput}>5</button>
            <button value={sup ? "\u2076" : "6"} className="n6" onMouseUp={addtoInput}>6</button>
            <button value={sup ? "\u2077" : "7"} className="n7" onMouseUp={addtoInput}>7</button>
            <button value={sup ? "\u2078" : "8"} className="n8" onMouseUp={addtoInput}>8</button>
            <button value={sup ? "\u2079" : "9"} className="n9" onMouseUp={addtoInput}>9</button>    
            
            <button value="." className="period" onMouseUp={addtoInput}>.</button>
            <button value="%" className="percent" onMouseUp={addtoInput}>%</button>
            <button value={"\u00F7"} className="divide" onMouseUp={addtoInput}>/</button>
            <button value={"\u00D7"} className="multiply" onMouseUp={addtoInput}>x</button>
            <button value="-"className="minus" onMouseUp={addtoInput}>-</button>
            <button value="+" className="addition" onMouseUp={addtoInput}>+</button>
            <button className="equal" onMouseUp={equal}>=</button>
                <button className="clear" onMouseUp={clear}>C</button>
                <button style={{ background: sup ? "green" : "orange", position: "absolute" }}
                    onMouseUp={superscript}>sup</button>
                
            </div>
        </div>
    )
}

export default Advanced
